import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
// import SeoScoreCalculator from "./SeoChecker";
import SeoChecker from "./SeoChecker";
import {
  Save,
  ArrowRight,
  Image,
  Plus,
  X,
  Loader,
  CheckCircle,
  AlertCircle,
} from "lucide-react";
import { motion } from "framer-motion";
import toast from "react-hot-toast";

const AddBlog = () => {
  const navigate = useNavigate();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState("");
  const [seoStatus, setSeoStatus] = useState({
    titleLength: {
      status: "warning",
      message: "عنوان خیلی کوتاه است (حداقل 30 کاراکتر)",
    },
    contentLength: {
      status: "error",
      message: "محتوا حداقل باید 300 کلمه باشد",
    },
    keywordDensity: {
      status: "warning",
      message: "تراکم کلمات کلیدی مناسب نیست",
    },
    metaDescription: { status: "error", message: "توضیحات متا تعریف نشده است" },
    headings: {
      status: "warning",
      message: "از عناوین H2 و H3 بیشتری استفاده کنید",
    },
    images: { status: "error", message: "تصویر شاخص ندارد" },
    internalLinks: { status: "warning", message: "لینک داخلی کافی نیست" },
  });

  const [formData, setFormData] = useState({
    title: "",
    content: "",
    excerpt: "",
    metaDescription: "",
    featured_image: null,
    keywords: "",
    status: "published",
    tags: [],
    seo_score: 0,
    category: "",
    seo_score_color: "text-red-500",
    author: "",
  });

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/blog/"
      : "https://allmaxsport.com/api/blog/";

  const IMAGE_API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/upload-images/"
      : "https://allmaxsport.com/api/upload-images/";

  const [seoScore, setSeoScore] = useState({
    score: 0,
    color: "text-red-500",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const addTag = () => {
    if (tagInput && !tags.includes(tagInput)) {
      setTags([...tags, tagInput]);
      setTagInput("");
    }
  };

  const removeTag = (tagToRemove) => {
    setTags(tags.filter((tag) => tag !== tagToRemove));
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      // In a real application, you would handle the file upload
      setFormData({
        ...formData,
        featuredImage: file,
      });

      updateSeoStatus("images", "success", "تصویر شاخص اضافه شده است");
    }
  };

  const updateSeoStatus = (key, status, message) => {
    setSeoStatus((prev) => ({
      ...prev,
      [key]: { status, message },
    }));
  };

  const calculateSeoScore = () => {
    // Calculate SEO score based on form data
    let score = 0;
    let totalChecks = Object.keys(seoStatus).length;

    // Title check
    if (formData.title.length >= 30 && formData.title.length <= 60) {
      score += 1;
      updateSeoStatus("titleLength", "success", "طول عنوان مناسب است");
    } else if (formData.title.length > 0) {
      updateSeoStatus(
        "titleLength",
        "warning",
        "طول عنوان مناسب نیست (بین 30 تا 60 کاراکتر)"
      );
    }

    // Content length check (rough word count)
    const wordCount = formData.content.split(/\s+/).filter(Boolean).length;
    if (wordCount >= 300) {
      score += 1;
      updateSeoStatus(
        "contentLength",
        "success",
        `محتوا ${wordCount} کلمه دارد (عالی)`
      );
    } else if (wordCount >= 100) {
      updateSeoStatus(
        "contentLength",
        "warning",
        `محتوا ${wordCount} کلمه دارد (حداقل 300 کلمه توصیه می‌شود)`
      );
    }

    // Meta description check
    if (
      formData.metaDescription.length >= 120 &&
      formData.metaDescription.length <= 155
    ) {
      score += 1;
      updateSeoStatus("metaDescription", "success", "توضیحات متا مناسب است");
    } else if (formData.metaDescription.length > 0) {
      updateSeoStatus(
        "metaDescription",
        "warning",
        "طول توضیحات متا مناسب نیست (بین 120 تا 155 کاراکتر)"
      );
    }

    // Keyword density check - simplified for demo
    if (formData.keywords && formData.content) {
      const keywords = formData.keywords
        .split(",")
        .map((k) => k.trim().toLowerCase());
      let keywordFound = false;

      keywords.forEach((keyword) => {
        if (formData.content.toLowerCase().includes(keyword)) {
          keywordFound = true;
        }
      });

      if (keywordFound) {
        score += 1;
        updateSeoStatus(
          "keywordDensity",
          "success",
          "تراکم کلمات کلیدی مناسب است"
        );
      } else {
        updateSeoStatus(
          "keywordDensity",
          "warning",
          "کلمات کلیدی در متن استفاده نشده‌اند"
        );
      }
    }

    // Headings check - simplified, in real implementation would check actual HTML content
    if (
      formData.content.includes("<h2>") ||
      formData.content.includes("<h3>")
    ) {
      score += 1;
      updateSeoStatus(
        "headings",
        "success",
        "از عناوین H2 و H3 استفاده شده است"
      );
    }

    // Image check
    if (formData.featuredImage) {
      score += 1;
    }

    // Internal links check - simplified
    if (formData.content.includes('<a href="/')) {
      score += 1;
      updateSeoStatus("internalLinks", "success", "لینک داخلی استفاده شده است");
    }

    // Calculate percentage
    const percentage = Math.round((score / totalChecks) * 100);

    // Set color based on score
    let color = "text-red-500";
    if (percentage >= 70) color = "text-green-500";
    else if (percentage >= 40) color = "text-yellow-500";

    setSeoScore({
      score: percentage,
      color,
    });
  };

  // Update SEO score on form changes
  useEffect(() => {
    calculateSeoScore();
  }, [formData, tags]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    let tagNames = tags;

    let tagsObject = tagNames.map((tag) => tag);
    const imageForm = new FormData();

    imageForm.append("image", formData.featuredImage);

    try {
      // Upload image
      let imageUrl = null;
      const imageResponse = await fetch(IMAGE_API_URL, {
        method: "POST",
        body: imageForm,
      });
      if (!imageResponse.ok) return;

      const imageData = await imageResponse.json();
      imageUrl = imageData.image_urls[0];

      const blogData = {
        title: formData.title,
        content: formData.content,
        excerpt: formData.excerpt,
        meta_description: formData.metaDescription,
        keywords: formData.keywords,
        status: formData.status,
        category: formData.category,
        tags: tagsObject,
        seo_score: seoScore.score,
        seo_score_color: seoScore.color,
        featured_image: imageUrl,
        author: formData.author,
      };

      const response = await fetch(API_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(blogData),
      });

      let data = await response.json();

      toast.success("مقاله با موفقیت اضافه شد.");
    } catch (error) {
    } finally {
      setIsSubmitting(false);
      navigate("/dashboard/blogs");
    }
  };

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-2xl font-bold text-gray-100">افزودن مقاله جدید</h1>
        <Link
          to="/dashboard/blogs"
          className="flex items-center text-gray-200 hover:text-gray-100"
        >
          <ArrowRight className="w-5 h-5 ml-1" />
          بازگشت به مدیریت بلاگ
        </Link>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Main form */}
        <div className="md:col-span-2">
          <form
            onSubmit={handleSubmit}
            className="bg-white rounded-lg shadow-sm p-6"
          >
            <div className="mb-6">
              <label
                htmlFor="author"
                className="block mb-2 text-sm font-medium text-gray-700"
              >
                نویسنده مقاله <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                id="authorName"
                name="author"
                value={formData?.author}
                onChange={handleChange}
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="اسم نویسنده ی مقاله را وارد کنید..."
                required
              />
            </div>

            <div className="mb-6">
              <label
                htmlFor="title"
                className="block mb-2 text-sm font-medium text-gray-700"
              >
                عنوان مقاله <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                id="title"
                name="title"
                value={formData.title}
                onChange={handleChange}
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="عنوان مقاله را وارد کنید..."
                required
              />
              <p className="mt-1 text-sm text-gray-500">
                عنوان باید بین 30 تا 60 کاراکتر باشد. طول فعلی:{" "}
                {formData.title.length} کاراکتر
              </p>
            </div>

            <div className="mb-6">
              <label
                htmlFor="excerpt"
                className="block mb-2 text-sm font-medium text-gray-700"
              >
                خلاصه مقاله
              </label>
              <textarea
                id="excerpt"
                name="excerpt"
                value={formData.excerpt}
                onChange={handleChange}
                rows="2"
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="خلاصه کوتاهی از مقاله..."
              ></textarea>
              <p className="mt-1 text-sm text-gray-500">
                این متن در صفحه‌ی اصلی بلاگ و نتایج جستجو نمایش داده می‌شود
              </p>
            </div>

            <div className="mb-6">
              <label
                htmlFor="content"
                className="block mb-2 text-sm font-medium text-gray-700"
              >
                محتوای مقاله <span className="text-red-500">*</span>
              </label>
              <textarea
                id="content"
                name="content"
                value={formData.content}
                onChange={handleChange}
                rows="10"
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="محتوای مقاله را وارد کنید..."
                required
              ></textarea>
              <p className="mt-1 text-sm text-gray-500">
                می‌توانید از تگ‌های HTML برای قالب‌بندی متن استفاده کنید
              </p>
            </div>

            <div className="mb-6">
              <label className="block mb-2 text-sm font-medium text-gray-700">
                برچسب‌ها
              </label>
              <div className="flex flex-wrap mb-2 gap-2">
                {tags.map((tag, index) => (
                  <div
                    key={index}
                    className="flex items-center bg-blue-50 text-blue-700 px-3 py-1 rounded-full"
                  >
                    <span>{tag}</span>
                    <button
                      type="button"
                      onClick={() => removeTag(tag)}
                      className="ml-2 text-blue-700 hover:text-blue-900"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                ))}
              </div>
              <div className="flex">
                <input
                  type="text"
                  value={tagInput}
                  onChange={(e) => setTagInput(e.target.value)}
                  onKeyPress={(e) =>
                    e.key === "Enter" && (e.preventDefault(), addTag())
                  }
                  className="flex-1 p-3 border border-gray-300 rounded-r-none rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="برچسب جدید..."
                />
                <button
                  type="button"
                  onClick={addTag}
                  className="bg-blue-500 text-white rounded-l-lg p-3 hover:bg-blue-600"
                >
                  <Plus className="w-5 h-5" />
                </button>
              </div>
              <p className="mt-1 text-sm text-gray-500">
                برچسب‌ها به کاربران کمک می‌کنند تا محتوای مرتبط را پیدا کنند
              </p>
            </div>

            <div className="mb-6">
              <label
                htmlFor="category"
                className="block mb-2 text-sm font-medium text-gray-700"
              >
                دسته بندی مقاله <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                id="category"
                name="category"
                value={formData?.category}
                onChange={handleChange}
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder=" دسته بندی مقاله را وارد کنید..."
                required
              />
            </div>

            <div className="mb-6">
              <label
                htmlFor="featuredImage"
                className="block mb-2 text-sm font-medium text-gray-700"
              >
                تصویر شاخص
              </label>
              <div className="mt-1 flex items-center">
                {formData.featuredImage ? (
                  <div className="relative mr-2">
                    <img
                      src={URL.createObjectURL(formData.featuredImage)}
                      alt="Preview"
                      className="h-24 w-24 object-cover rounded-lg"
                    />
                    <button
                      type="button"
                      onClick={() =>
                        setFormData({ ...formData, featuredImage: null })
                      }
                      className="absolute -top-2 -right-2 bg-red-100 rounded-full p-1 text-red-500 hover:bg-red-200"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                ) : (
                  <label className="flex flex-col items-center justify-center w-24 h-24 border-2 border-gray-300 border-dashed rounded-lg cursor-pointer bg-gray-50 hover:bg-gray-100">
                    <div className="flex flex-col items-center justify-center pt-5 pb-6">
                      <Image className="w-8 h-8 mb-1 text-gray-500" />
                      <p className="text-xs text-gray-500">بارگذاری</p>
                    </div>
                    <input
                      type="file"
                      className="hidden"
                      onChange={handleImageChange}
                      accept="image/*"
                    />
                  </label>
                )}
              </div>
            </div>

            <hr className="my-6" />

            <div className="mb-6">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">
                اطلاعات سئو (SEO)
              </h3>

              <div className="mb-4">
                <label
                  htmlFor="metaDescription"
                  className="block mb-2 text-sm font-medium text-gray-700"
                >
                  توضیحات متا (Meta Description)
                </label>
                <textarea
                  id="metaDescription"
                  name="metaDescription"
                  value={formData.metaDescription}
                  onChange={handleChange}
                  rows="2"
                  className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="توضیحات متا را وارد کنید..."
                ></textarea>
                <p className="mt-1 text-sm text-gray-500">
                  بین 120 تا 155 کاراکتر. طول فعلی:{" "}
                  {formData.metaDescription.length} کاراکتر
                </p>
              </div>

              <div className="mb-4">
                <label
                  htmlFor="keywords"
                  className="block mb-2 text-sm font-medium text-gray-700"
                >
                  کلمات کلیدی (با کاما جدا کنید)
                </label>
                <input
                  type="text"
                  id="keywords"
                  name="keywords"
                  value={formData.keywords}
                  onChange={handleChange}
                  className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="کلمات کلیدی را وارد کنید..."
                />
              </div>
            </div>

            <div className="flex justify-between items-center">
              <div className="flex items-center">
                <select
                  name="status"
                  value={formData.status}
                  onChange={handleChange}
                  className="mr-4 p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="draft">پیش‌نویس</option>
                  <option value="published">انتشار</option>
                </select>
              </div>
              <div>
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="flex items-center px-6 py-3 bg-green-500 text-white rounded-lg shadow hover:bg-green-600 disabled:bg-green-300"
                >
                  {isSubmitting ? (
                    <>
                      <Loader className="w-5 h-5 ml-2 animate-spin" />
                      در حال ذخیره...
                    </>
                  ) : (
                    <>
                      <Save className="w-5 h-5 ml-2" />
                      ذخیره مقاله
                    </>
                  )}
                </button>
              </div>
            </div>
          </form>
        </div>

        {/* SEO Score Panel */}
        <SeoChecker
          formData={formData}
          seoScore={seoScore}
          seoStatus={seoStatus}
          updateSeoStatus={updateSeoStatus}
        />
      </div>
    </div>
  );
};

export default AddBlog;
